.card {
    display: inline-block;
}

img {
    width: 100px;
    margin: 10px 20px
}

img:hover {
    -webkit-filter: drop-shadow(5px 5px 5px #222);
    filter: drop-shadow(5px 5px 5px #222);
    -ms-filter: "progid:DXImageTransform.Microsoft.Dropshadow(OffX=12, OffY=12, Color='#444')";
    filter: "progid:DXImageTransform.Microsoft.Dropshadow(OffX=12, OffY=12, Color='#444')";
}

@media only screen and (max-width: 992px) {
    img {
        width: 95px;
        margin: 8px 17px
    }
  }
  @media only screen and (max-width: 768px) {
    img {
        width: 75px;
        margin: 5px 9px
    }
  }
  @media only screen and (max-width: 571px) {
    img {
        width: 60px;
        margin: 4px 5px
    }
  }
  @media only screen and (max-width: 425px) {
    img {
        width: 50px;
        margin: 3px 4px
    }
  }
  
p {
    text-align: center
}

nav.navbar.navbar-default {
  margin-bottom: 10px;
}

.navbar {
  background-color: rgba(53,84,100, 0.7) !important;
  height: 70px;
  -webkit-filter: drop-shadow(5px 5px 5px #222);
    filter: drop-shadow(5px 5px 5px #222);
}

.navbar-brand {
  font-family: 'Luckiest Guy';
  font-size: 3rem;
  color: rgb(52,176,165) !important
}

li {
  margin-right: 20px;
  font-size: 1.2rem; 
}

li a {
  color: rgb(52,176,165)
}

li a:hover {
  color: rgb(82,206,195);
  text-decoration: none
}

@media only screen and (max-width: 992px) {
  .navbar-brand {
    font-size: 3rem;
  }
}
@media only screen and (max-width: 768px) {
  .navbar-brand {
    font-size: 2.5rem;
  }
}
@media only screen and (max-width: 571px) {
  .navbar-brand {
    font-size: 1.7rem;
  }
}
@media only screen and (max-width: 425px) {
  .navbar-brand {
    font-size: 1rem;
  }
  li {
    margin-right: 20px;
    font-size: 0.9rem; 
  }
}

.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

.scoreRow {
    margin-top: 85px;
    text-align: center;
    font-size: 1.4rem;
    color: white
}

.win {
    color:green
}

.lose {
    color: red
}
.footer {
  background-color: rgba(53,84,100, 0.7) !important;
  position: relative;
}

.footer span {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color: rgba(53,84,100, 0.7) !important;
  color: rgb(211,255,255)

}


